.btn {
  display: inline-block;
  padding: 15px 1.5em;
  border-radius: 0.3rem;
  background: white;
  text-align: center;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.16);
  font-size: 14px;
  text-decoration: none;
  transition: 0.2s;
}
.btn.round {
  border-radius: 44px;
}
.btn.size-3 {
  min-width: 3em;
}
.btn.size-6 {
  min-width: 6em;
}
.btn.size-9 {
  min-width: 9em;
}
.btn.size-block {
  display: block;
}
.btn.btn-theme {
  background-color: #4FB200;
  color: white;
}
.btn.btn-danger {
  background-color: #FF0000;
  color: white;
}
.btn.btn-success {
  background-color: #006CFF;
  color: white;
}
.btn.btn-disabled {
  background-color: #DCDCDC;
  color: #9C9C9C;
}
.btn.btn-b {
  border: solid 1px black;
  color: black;
}
.btn.btn-b-theme {
  border: solid 1px #4FB200;
  color: #4FB200;
}
.btn.btn-b-danger {
  border: solid 1px #FF0000;
  color: #FF0000;
}
.btn.btn-b-success {
  border: solid 1px #006CFF;
  color: #006CFF;
}
.btn.btn-b-disabled {
  background-color: #DCDCDC;
  border: solid 1px #9C9C9C;
  color: #9C9C9C;
}
.btn:hover {
  -webkit-backface-visibility: hidden;
  cursor: pointer;
  opacity: 0.6;
}

.card {
  margin-bottom: 1em;
  box-shadow: 0 0 15px -6px #00000073;
  background-color: white;
  width: 17em;
}
.card-img {
  width: 100%;
}
.card-body {
  padding: 1.5em;
}
.card p {
  margin-bottom: 1em;
}

label {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.input-wrap {
  display: block;
  padding: 1.2em 0;
  min-width: 16em;
}
.input-wrap.inline {
  width: 100%;
  min-width: 15em;
  max-width: 20em;
}
.input-wrap .input-label {
  position: relative;
  z-index: 0;
  margin: 0;
  width: 100%;
  height: 46px;
  box-sizing: border-box;
  background: transparent;
}
.input-wrap .input-label label {
  position: absolute;
  z-index: 0;
  color: gray;
  transition: 0.2s;
  font-size: 1em;
  box-sizing: border-box;
}
.input-wrap .input-label input {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  box-sizing: border-box;
}
.input-wrap .input-label input:placeholder-shown + label {
  top: 0.6em;
  left: 1.2em;
}
.input-wrap .input-label input:not(placeholder-shown) + label {
  top: -1.5em;
  left: 0em;
}
.input-wrap .input-label.error {
  margin-bottom: 0.2em;
}
.input-wrap .input-label.error input {
  box-sizing: border-box;
  box-shadow: 0 0 0 0.2rem rgba(255, 0, 0, 0.5);
}
.input-wrap .input-label.error + p {
  margin-left: 1em;
  color: #FF0000;
  font-size: 14px;
}

input {
  box-sizing: border-box;
}
input[type] {
  appearance: none;
}
input:focus {
  outline: none;
  box-shadow: 0 0 0 0.2rem rgba(0, 108, 255, 0.5) !important;
}
input[type=text], input[type=password] {
  padding: 15px 20px;
  font-size: 16px;
  border-radius: 0.5em;
  border: solid 1px black;
}
input[type=text].round, input[type=password].round {
  border-radius: 45px;
}
input[type=radio] {
  margin: 5px;
  position: relative;
  border-radius: 50%;
  border: 1px solid black;
  width: 24px;
  height: 24px;
}
input[type=radio]:before {
  content: "";
  display: block;
  box-shadow: 0 0 2px 0.2rem rgba(0, 108, 255, 0.5);
  border-radius: 50%;
  position: absolute;
  left: 7px;
  top: 7px;
  margin: auto;
  width: 8px;
  height: 8px;
  background: #006CFF;
  transform: scale(0);
  transition: 0.2s;
}
input[type=radio]:checked {
  border: 1px solid #006CFF;
  box-shadow: 0 0 0 0.2rem rgba(0, 108, 255, 0.5);
}
input[type=radio]:checked:before {
  transform: scale(1);
}
input[type=checkbox] {
  margin: 5px;
  position: relative;
  border: 1px solid black;
  border-radius: 5px;
  width: 24px;
  height: 24px;
  box-shadow: 0 0 0 0;
}
input[type=checkbox]:before {
  content: "";
  display: block;
  position: absolute;
  left: 7px;
  top: 2px;
  margin: auto;
  width: 5px;
  height: 13px;
  border-right: 3px solid #006CFF;
  border-bottom: 3px solid #006CFF;
  transform: scale(0) rotate(90deg);
  transition: 0.2s;
}
input[type=checkbox]:checked {
  border: 1px solid #006CFF;
  box-shadow: 0 0 0 0.2rem rgba(0, 108, 255, 0.5);
}
input[type=checkbox]:checked:before {
  transform: scale(1) rotate(45deg);
}
input[type=submit] {
  box-shadow: 0 0 0 0;
}

.content-main {
  background: white;
  margin: 0.5em;
  padding: 0.5em;
}

.content-block {
  margin-bottom: 1em;
}
.content-block.center {
  text-align: center;
}
.content-block .content-block-horizontal {
  text-align: left;
}

.content-block-horizontal {
  display: inline-block;
  margin-bottom: 1em;
  vertical-align: top;
}
.content-block-horizontal.align-middle {
  vertical-align: middle;
}
.content-block-horizontal.align-bottom {
  vertical-align: bottom;
}

.content-indent {
  margin-left: 1em;
}

.text-center {
  text-align: center;
}

@media screen and (min-width: 415px) {
  .content-indent {
    margin-left: 2em;
  }
}
@media screen and (min-width: 960px) {
  .content-main {
    margin: 0.5em auto;
    max-width: 50em;
  }
}
/***************************
    flexitems
***************************/
.content-flex {
  display: flex;
}
.content-flex div:not(:last-of-type) {
  margin-right: 0.5em;
}
.content-flex.column {
  flex-direction: column;
}
.content-flex.column div:not(:last-of-type) {
  margin-right: 0;
  margin-bottom: 0.5em;
}
.content-flex.center {
  justify-content: center;
}
.content-flex.end {
  justify-content: flex-end;
}
.content-flex.between {
  justify-content: space-between;
}
.content-flex.align-start {
  align-items: flex-start;
}
.content-flex.align-center {
  align-items: center;
}
.content-flex.align-end {
  align-items: flex-end;
}
.content-flex.flex-wrap {
  flex-wrap: wrap;
}

.table-wrap {
  overflow: scroll;
  width: 100%;
}

table {
  border-collapse: collapse;
  margin-bottom: 16px;
  box-shadow: 0 0 15px -6px #00000073;
  background-color: white;
  width: 600px;
}
table.center {
  margin: 0 auto 16px;
}
table tr {
  box-sizing: border-box;
}
table tr:not(:nth-of-type(1)) {
  border-top: 1px solid black;
}
table th {
  background-color: #4FB200;
  color: white;
  padding: 1rem;
  white-space: nowrap;
}
table td {
  padding: 0.75rem;
}

/***************************
    .nav
***************************/
.sp-only .nav-btn {
  position: absolute;
  top: 1.25em;
  height: 1.5em;
  opacity: 0.4;
  z-index: 9998;
  animation: show 0.4s forwards;
  transition: 0.3s;
}
.sp-only .nav-btn.nav-fixed {
  position: fixed;
}
.sp-only .nav-btn.nav-right {
  right: 1.25em;
}
.sp-only .nav-btn.nav-right.is-active {
  right: calc(70% + 1.25em);
  opacity: 1;
}
.sp-only .nav-btn.nav-left {
  left: 1.25em;
}
.sp-only .nav-btn.nav-left.is-active {
  left: calc(70% + 1.25em);
  opacity: 1;
}
.sp-only .nav-btn:hover {
  opacity: 1;
}
.sp-only .nav-menu {
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 9999;
  width: 70%;
  height: 100vh;
  height: -webkit-fill-available;
  background-color: white;
  transition: 0.3s;
}
.sp-only .nav-menu.nav-right {
  right: calc(-70%);
}
.sp-only .nav-menu.nav-right.is-active {
  right: 0;
}
.sp-only .nav-menu.nav-right.is-active + .nav-bg {
  display: block;
}
.sp-only .nav-menu.nav-left {
  left: calc(-70%);
}
.sp-only .nav-menu.nav-left.is-active {
  left: 0;
}
.sp-only .nav-menu.nav-left.is-active + .nav-bg {
  display: block;
}
.sp-only .nav-bg {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9997;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.pc-only .nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 1em;
  position: absolute;
  z-index: 9999;
  top: 1.5em;
  left: calc(50% - 12em);
  width: 24em;
  height: 7em;
  border-radius: 1.5em;
  background-color: white;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.16);
  animation: show 0.4s forwards;
}
.pc-only .nav-menu.nav-fixed {
  position: fixed;
}
.pc-only .nav-item:not(:last-of-type) {
  margin-right: 1em;
}

@keyframes show {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
.nav-space {
  padding-bottom: calc(1.5em + 2.5em);
}

@media screen and (min-width: 415px) {
  .nav-space {
    padding-top: calc(7em + 2em);
    padding-bottom: calc(7em + 2em);
  }
}
